@keyframes topOpen {
  0% {
    top: 0;
    transform: rotate(0);
  }
  50% {
    top: 10px;
    transform: rotate(0);
  }
  100% {
    top: 10px;
    transform: rotate(45deg);
  }
}
@keyframes topClose {
  0% {
    top: 10px;
    transform: rotate(45deg);
  }
  50% {
    top: 10px;
    transform: rotate(0);
  }
  100% {
    top: 0;
    transform: rotate(0);
  }
}
@keyframes mdlOpen {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes mdlClose {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes btmOpen {
  0% {
    bottom: 0;
    transform: rotate(0);
  }
  50% {
    bottom: 10px;
    transform: rotate(0);
  }
  100% {
    bottom: 10px;
    transform: rotate(-45deg);
  }
}
@keyframes btmClose {
  0% {
    bottom: 10px;
    transform: rotate(-45deg);
  }
  50% {
    bottom: 10px;
    transform: rotate(0);
  }
  100% {
    bottom: 0;
    transform: rotate(0);
  }
}
body {
  font-family: "Rubik", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  min-width: 360px;
  color: #ffffff;
  background: #2e2e2e;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-nav {
  position: relative;
}

.main-nav__nav-head {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
}

.main-nav__logo {
  display: block;
  margin: 10px 85px 0 85px;
  transition: 0.5s;
}
.main-nav__logo:hover, .main-nav__logo:focus {
  opacity: 0.7;
  transition: 0.5s;
}

.main-nav__button-block {
  width: 100%;
  height: 60px;
  transition: 0.1s;
}
.main-nav__button-block .main-nav__menu-button span:nth-of-type(1) {
  animation: ease 0.5s topClose forwards;
}
.main-nav__button-block .main-nav__menu-button span:nth-of-type(2) {
  animation: ease 0.5s mdlClose forwards;
}
.main-nav__button-block .main-nav__menu-button span:nth-of-type(3) {
  animation: ease 0.5s btmClose forwards;
}
.main-nav__button-block--open {
  background: #1c1c1c;
  opacity: 0.95;
  transition: 0.6s;
}
.main-nav__button-block--open .main-nav__menu-button span:nth-of-type(1) {
  animation: ease 0.5s topOpen forwards;
}
.main-nav__button-block--open .main-nav__menu-button span:nth-of-type(2) {
  animation: ease 0.5s mdlOpen forwards;
}
.main-nav__button-block--open .main-nav__menu-button span:nth-of-type(3) {
  animation: ease 0.5s btmOpen forwards;
}

.main-nav__menu-button {
  position: absolute;
  display: block;
  top: 35px;
  right: 30px;
  width: 26px;
  height: 20px;
  background-color: transparent;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
}
.main-nav__menu-button span {
  position: relative;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  border-radius: 30px;
}
.main-nav__menu-button span + span {
  margin-top: 8px;
}

.main-nav__list {
  letter-spacing: 2px;
  text-align: right;
  position: absolute;
  top: 60px;
  width: 100%;
  margin: 0;
  padding: 30px 0 40px 0;
  list-style: none;
  opacity: 0.95;
  z-index: 3;
  transform: translateY(-110%);
  transition: 0.5s;
}
.main-nav__list--open {
  background: #1c1c1c;
  opacity: 0.95;
  transform: translateY(0);
  transition: 0.5s;
}

.main-nav__item a {
  text-decoration: none;
  color: #ffffff;
  transition: 0.5s;
}
.main-nav__item a:hover, .main-nav__item a:focus {
  opacity: 0.7;
  transition: 0.5s;
}
.main-nav__item + .main-nav__item {
  margin-top: 10px;
}